import React, { Component } from 'react'
import '../styles/markdown.css';
import Remarkable from "remarkable"
export default class MarkdownEditor extends Component {
constructor(props){
  super(props)
  this.state={
    text:'Enter your **Markdown** . .'}
}
handleChange= (event)=>{
  this.setState({text: event.target.value})
}

getRawMarkup(){
  const md = new Remarkable();
  return {__html:md.render(this.state.text)}
}

  render() {
    return (
      <div className="markdownEditor">
       
        <form  className="row">
 <div className="col-md-6">
 <h2>INPUT</h2>
 <br></br>
 <textarea rows="4" id="markdown-co" defaultValue={this.state.text} onChange={this.handleChange} required ></textarea></div>
 <div  className="col-md-6">
 <h2>OUTPUT</h2>
 <br></br>
 <div className="output" dangerouslySetInnerHTML={this.getRawMarkup()}></div></div>
 

</form>

      </div>
    )
  }
}
